@import url('https://fonts.googleapis.com/css2?family=Vazirmatn:wght@100;200;300;400;500;600;700;800;900&display=swap');

:root {
    --coffee-dark: #3c2a21;
    --coffee-medium: #5c3d2e;
    --coffee-light: #b85c38;
    --coffee-cream: #e6c9a8;
    --coffee-bg: #f8f3e9;
}

* {
    font-family: 'Vazirmatn', sans-serif;
}

body {
    background-color: var(--coffee-bg);
    margin: 0;
    padding: 0;
}

.navbar {
    background-color: var(--coffee-dark);
}

.hero {
    background: linear-gradient(rgba(60, 42, 33, 0.8), rgba(60, 42, 33, 0.8)), 
                url('https://images.unsplash.com/photo-1447933601403-0c6688de566e?ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80');
    background-size: cover;
    background-position: center;
    color: white;
    padding: 100px 0;
    text-align: center;
}

.product-card {
    transition: all 0.3s ease;
    border: none;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    height: 100%;
    overflow: hidden;
}

.product-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

.product-image {
    height: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, var(--coffee-medium) 0%, var(--coffee-dark) 100%);
    color: white;
    font-size: 3rem;
    position: relative;
}

.category-badge {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 10px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 600;
    z-index: 10;
}

.category-robusta {
    background-color: #3c2a21;
    color: white;
}

.category-arabica {
    background-color: #b85c38;
    color: white;
}

.category-mix {
    background-color: #e6c9a8;
    color: #3c2a21;
}

.category-turkish {
    background-color: #6d4c41;
    color: white;
}

.price-tag {
    position: absolute;
    bottom: 10px;
    left: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    color: var(--coffee-dark);
    padding: 5px 10px;
    border-radius: 20px;
    font-weight: 700;
}

.btn-coffee {
    background-color: var(--coffee-light);
    color: white;
    border: none;
    transition: all 0.3s ease;
}

.btn-coffee:hover {
    background-color: var(--coffee-medium);
    color: white;
    transform: translateY(-2px);
}

.btn-outline-coffee {
    border: 2px solid var(--coffee-medium);
    color: var(--coffee-medium);
    background-color: transparent;
    transition: all 0.3s ease;
}

.btn-outline-coffee:hover {
    background-color: var(--coffee-medium);
    color: white;
}

.section-title {
    color: var(--coffee-medium);
    font-weight: 700;
    margin-bottom: 2rem;
    position: relative;
    display: inline-block;
}

.section-title::after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 0;
    width: 50%;
    height: 3px;
    background-color: var(--coffee-light);
}

.filter-btn {
    border: none;
    background: transparent;
    color: var(--coffee-medium);
    font-weight: 600;
    padding: 8px 16px;
    border-radius: 20px;
    transition: all 0.3s ease;
}

.filter-btn:hover, .filter-btn.active {
    background-color: var(--coffee-light);
    color: white;
}

.footer {
    background-color: var(--coffee-dark);
    color: white;
    padding: 3rem 0;
}

.footer-title {
    color: var(--coffee-cream);
    font-weight: 700;
    margin-bottom: 1.5rem;
}

.footer-link {
    color: white;
    text-decoration: none;
    transition: all 0.3s ease;
    display: block;
    margin-bottom: 0.5rem;
}

.footer-link:hover {
    color: var(--coffee-cream);
    transform: translateX(-5px);
} 